<template lang="pug">
  .searchInput
    b-form-input(
      v-model='valueInner'
      class="cus-input"
      autocomplete="off"
      :placeholder="placeholder"
      @compositionstart="isTyping = true"
      @compositionend="isTyping = false"
    )
    b-icon-x.clear(
      v-if="isTyping || valueInner"
      role="button"
      @click="valueInner = ''"
    )
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: () => null
    },
    placeholder: {
      type: String,
      default: '請輸入用戶名'
    }
  },
  data() {
    return {
      isTyping: false,
    }
  },
  computed: {
    valueInner: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      }
    }
  },
}
</script>

<style lang="scss">
.searchInput {
  width: 100%;
  position: relative;
  .cus-input {
    height: 31px;
    font-size: 14px;
    &:focus {
      box-shadow: none;
    }
  }
  .clear {
    font-size: 18px;
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translate(-130%, -50%);
  }
}
</style>